.backToPacks {
    position: absolute;
    top: 0;
    left: 0;
}

@media screen and (max-width: 1265px) {
    .backToPacks {
        left: 35px;
    }
}

@media screen and (max-height: 650px) {
    .backToPacks {
        padding: 25px 0;
        position: relative;
        top: 0;
        left: 0;
    }
}

.title{
    margin: 0 0 30px;
}

.ava{
    position: relative;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-position: center;
    background-size: contain;
}

.buttonWrapper{
    position: absolute;
    bottom: 0;
    right: 0;
    color: white;
    border: 1px  #ffffff solid;
    border-radius: 50%;
    background-color: #808080;
}

.button{
    width: 30px;
    height: 30px;
}

.email{
    margin-bottom: 29px;
}

.logOut{
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3);
    filter: drop-shadow(0px 2px 10px rgba(109, 109, 109, 0.25));
}

.text{
    position: relative;
    bottom: -1px;
}